<template>
  <header class="nav-bar">
    <div v-if="headerData.leftText" class="nav-bar-left">
      <div @click.stop="handelClick(headerData.leftUrl)">
        <i class="iconfont iconicon-back" />
        <div class="nav-bar-txt">{{ headerData.leftText }}</div>
      </div>
    </div>
    <div class="nav-bar-title">{{ headerData.title }}</div>
    <div class="nav-bar-right">
      <div
        v-if="headerData.rightText"
        class="nav-bar-txt"
        @click="handelClick(headerData.rightUrl, headerData.rightCallBackClick)"
      >{{ headerData.rightText }}</div>
      <i v-if="headerData.showSearch" class="iconfont iconsousuo" @click="searchClick()" />
    </div>
  </header>
</template>

<script type='es6'>
export default {
  name: 'HeaderBar',
  props: {
    headerData: {
      type: null,
      required: true
    }
  },
  data() {
    return {}
  },
  created() {
    /**
     * headerData 目前参数
       headerData: {
        leftText: "",
        leftUrl: "", // 需要跳转的路由，没有则不传
        title: "",
        rightText: "",
        rightUrl: ""; // 需要跳转的路由，没有则不传
        rightCallBackClick: true; // 是否需要回传点击事件
        showSearch:"" //是否展示搜索按钮
      }
     */

    // 初始化 headerData 数据
    if (this.headerData) {
      if (!this.headerData.rightCallBackClick) {
        this.headerData.rightCallBackClick = false
      }
    }
  },
  methods: {
    searchClick() {
      // this.$emit('searchClick', '')
      this.$bus.$emit('showMeun', true)
    },
    handelClick(targetUrl, callBackClick) {
      console.log('targetUrl=====================', targetUrl)

      if (targetUrl === 'back') {
        this.$router.go(-1)
        return
      }
      if (targetUrl === 'needConfirm') {
        this.$emit('on-exit')
        return
      }
      if (targetUrl) {
        this.$router.push(targetUrl)
        return
      }
      if (callBackClick) {
        let rightText = 'callback'
        if (this.headerData && this.headerData.rightText) {
          rightText = this.headerData.rightText
        }
        this.$emit('callbackClick', rightText)
      }
    }
  }
}
</script>

<style lang="less" scoped>
</style>
