<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <!-- 新建收货地址 begin -->
      <div class="content">
        <div class="common-main box">

          <van-field
            v-model="name"
            v-validate="'required'"
            required
            label="收货人姓名"
            name="name"
            placeholder="请输入收货人姓名"
            :error-message="errors.first('name')"
          />

          <van-field
            v-model="sjh"
            v-validate="'required|phone'"
            label="手机号码"
            name="sjh"
            required
            placeholder="请输入收货人手机号"
            :error-message="errors.first('sjh')"
          />

          <van-field
            v-model="gddh"
            label="固定电话"
            placeholder="请输入固定电话"
          />

          <van-field
            v-model="dzyx"
            v-validate="'required|email'"
            required
            label="电子邮箱"
            placeholder="请输入电子邮箱"
            name="email"
            :error-message="errors.first('email')"
          />

          <van-field
            v-model="yb"
            v-validate="'required'"
            required
            label="邮编"
            placeholder="请输入邮编"
            name="yb"
            :error-message="errors.first('yb')"
          />

          <van-field
            v-model="addressmodel"
            v-validate="'required|address'"
            required
            label="所在地区"
            placeholder="请选择所在地区"
            name="address"
            :error-message="errors.first('address')"
            is-link
            readonly
            @click="showPopup"
          />

          <!-- <span class="tit">所在地区</span>
              <span :v-model="addressmodel" class="info">{{ addressmodel }}</span> -->

          <van-field
            v-model="message"
            v-validate="'required'"
            required
            rows="2"
            label="所在街道"
            name="jtdz"
            :error-message="errors.first('jtdz')"
            autosize
            type="textarea"
            maxlength="50"
            placeholder="请填写街道、楼道号等"
          />

        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <div class="spe-list">
            <div class="spe-left"><span class="tit">设为默认地址</span></div>
            <div class="spe-right">
              <van-switch v-model="checked" size="20px" />
            </div>
          </div>
        </div>
      </div>
      <!-- 新建收货地址 end -->
    </div>
    <div class="common-space" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn one fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="saveAdress()">保存</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->

    <!-- 收货地址弹出层 beign -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '70%' }"
    >
      <!-- <van-button plain type="info" style="float:left;border: none;">取消</van-button>
      <van-button plain type="info" style="float:right;border: none;">确定</van-button> -->
      <!-- <van-area ref="myArea" :area-list="areaList" :columns-num="3" @change="onChange" @confirm="onConfirm" @cancel="onCancel" /> -->
      <XyAddress @getCheckResult="getCheckResult" />
    </van-popup>
    <!-- 收货地址弹出层 end -->
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import XyAddress from '@/components/address/address.vue'

import { saveAdress } from '@/store/module.js'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'AddressAdd',
  components: {
    headerBar,
    XyAddress
  },
  // mixins: [pageRouter],
  data() {
    return {
      name: '', // 收货人姓名
      sjh: '', // 收货人手机号
      message: '', // 所在街道
      gddh: '', // 固定电话
      dzyx: '', // 电子邮箱
      yb: '', // 邮编
      sheng: '', // 省
      shi: '', // 市
      xian: '', // 县
      xiang: '', // 乡镇
      checked: true,
      show: false,
      addressmodel: '',
      address: '',
      account: '',
      id: '',
      headerData: {
        title: '新建收货地址',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  created() {
    this.headerData.title = this.$route.params.title
    this.address = this.$route.params.address
    if (this.address) {
      this.id = this.address.id
      this.account = this.address.account
      this.name = this.address.consignee
      this.sjh = this.address.mobile
      this.gddh = this.address.tel
      this.dzyx = this.address.email
      this.yb = this.address.postcode
      this.checked = this.address.default_address === 1
      this.sheng = this.address.province
      this.shi = this.address.city
      this.xian = this.address.county
      this.xiang = this.address.town
      this.message = this.address.address
      this.addressmodel = this.address.area
    }
  },
  methods: {
    // 获取地址组件返回值
    getCheckResult(sheng, shi, xian, xiang, state) {
      if (!state) {
        this.show = false
      }
      if (sheng) {
        this.addressmodel = sheng.text
        this.sheng = sheng.value
      }
      if (shi) {
        this.addressmodel = this.addressmodel + '/' + shi.text
        this.shi = shi.value
      }
      if (xian) {
        this.addressmodel = this.addressmodel + '/' + xian.text
        this.xian = xian.value
      }
      if (xiang) {
        this.addressmodel = this.addressmodel + '/' + xiang.text
        this.xiang = xiang.value
      }
    },
    saveAdress() {
      this.$validator.validate().then(result => {
        if (result) {
          const pre = {
            id: this.id,
            consignee: this.name,
            mobile: this.sjh,
            tel: this.gddh,
            email: this.dzyx,
            postcode: this.yb,
            default_address: this.checked ? 1 : 0,
            province: this.sheng ? this.sheng : '',
            city: this.shi ? this.shi : '',
            county: this.xian ? this.xian : '',
            town: this.xiang ? this.xiang : '',
            address: this.message,
            account: this.account
          }
          saveAdress.data = pre
          this.asyncApiModule(saveAdress).then(res => {
            this.$toast.success('保存成功')
            setTimeout(() => {
              this.$toast.clear()
              this.$router.back()
            }, 1000)
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },
    goBack() {
      this.$router.back()
    },
    showPopup() {
      this.show = true
    },
    // value=0改变省，1改变市，2改变区
    onChange(picker, index, value) {
      const val = picker.getValues()
      console.log(val)// 查看打印
      let areaName = ''
      for (var i = 0; i < val.length; i++) {
        areaName = areaName + (i === 0 ? '' : '/') + val[i].name
      }
      this.addressmodel = areaName
    },
    // 确定选择城市
    onConfirm(val) {
      console.log('val------------------', val)
      this.sheng = val[0].code
      this.shi = val[1].code
      this.qu = val[2].code
      this.addressmodel = val[0].name + '/' + val[1].name + '/' + val[2].name
      this.show = false// 关闭弹框
    },
    // 取消选中城市
    onCancel() {
      this.show = false
      this.$refs.myArea.reset()// 重置城市列表
    }
  }
}
</script>

<style lang="css" scoped>

</style>
